          p.mb-4 La potabilizacin del agua en contextos rurales es una necesidad crtica para garantizar el derecho humano al agua segura, reducir riesgos sanitarios y proteger la salud de las familias campesinas. Aunque muchos predios carecen de infraestructura convencional, existen tcnicas accesibles y efectivas que pueden implementarse con recursos locales y bajo costo. Entre las alternativas ms utilizadas se encuentran:
    .row.mb-4
