      h2 2.3 Tcnicas y estrategias concretas de manejo y conservacin
    p.mb-5 Las tcnicas y estrategias concretas de manejo y conservacin renen un conjunto de acciones prcticas orientadas a fortalecer la biodiversidad en los predios rurales mediante principios agroecolgicos, conocimientos locales y lineamientos establecidos por la normatividad colombiana.
    .row.mb-4.ms-0
      .col-auto.bg-c5(data-aos="fade-left")
        .row.align-items-end
          .col-auto.px-0
            figure
              img(src='@/assets/curso/tema1/img4.png', alt='', style='width: 40px; position: relative; left: 5px').m-auto
          .col.ps-0
            h3.mb-2.ms-3 Conservacin de remanentes (relictos) y corredores biolgicos
    p.mb-4 La conservacin de remanentes de bosque y de corredores biolgicos constituye una estrategia central para mantener la conectividad ecolgica, preservar la biodiversidad y asegurar la provisin de servicios ecosistmicos en los predios rurales. En Colombia, un pas con una biodiversidad excepcional y con una presin creciente sobre los ecosistemas naturales, esta prctica adquiere un papel determinante dentro del marco legal que protege los bosques y promueve la restauracin ecolgica.
    .row.mb-5
      .col-lg-3.mb-4.mb-lg-0(data-aos="fade-right")
        figure
          img(src='@/assets/curso/tema2/img19.png', style="width: 300px").m-auto
      .col-lg-9(data-aos="fade-left")
        AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
          .row(titulo="Qu son los remanentes y por qu importan?")
            .col-12
              p.mb-0 Los remanentes corresponden a fragmentos de bosque natural que han permanecido a lo largo del tiempo, incluso bajo presiones antrpicas intensas. Se asemejan a huellas antiguas del territorio, espacios donde la vida contina su ciclo sin interrupcin. En Colombia, donde el equilibrio entre desarrollo y conservacin siempre est en tensin, estos relictos cumplen una funcin decisiva. Albergan una riqueza biolgica notable y garantizan estabilidad ecolgica, regulacin climtica, refugio de fauna y servicios ecosistmicos esenciales. La normatividad ambiental colombiana, respaldada por el Cdigo de Recursos Naturales Renovables, la Ley 99 de 1993 y sus reglamentaciones, reconoce el valor de estos remanentes. Desde all se orienta su proteccin, estableciendo que cualquier intervencin requiere estudios, permisos y criterios de manejo responsables. Conservarlos no es solo una decisin voluntaria; constituye una responsabilidad legal y un aporte tangible al bienestar colectivo.
          .row(titulo="Corredores biolgicos: puentes vivos para la biodiversidad")
            .col-12
              p.mb-0 Los corredores biolgicos funcionan como enlaces verdes que permiten el desplazamiento de fauna, el flujo gentico y la continuidad de procesos ecolgicos entre fragmentos aislados. Son estructuras vivas que transforman un paisaje fragmentado en un territorio conectado, donde la vida circula con libertad. En predios rurales colombianos, su implementacin evita la ruptura de hbitats, aumenta la diversidad gentica y favorece la resiliencia de los ecosistemas. Estos corredores pueden ser muy simples: franjas de rboles nativos junto a un cauce de agua, arbustos distribuidos estratgicamente para la movilidad de especies pequeas o setos vivos que brindan alimento y refugio. No se requieren inversiones extraordinarias, sino conocimiento de las especies nativas, compromiso territorial y una actitud cuidadosa frente a los procesos ecolgicos.
          .row(titulo="Cmo conservar remanentes y crear corredores en la finca?")
            .col-12
              p.mb-0 Actuar sobre estos espacios implica protegerlos de talas, quemas o presiones que comprometan su integridad. Esto incluye mantenerlos libres de especies invasoras, aplicar cercas vivas para delimitar su rea, instalar sealizacin y promover prcticas responsables entre quienes viven o trabajan en la finca. Cuidar los remanentes es similar a custodiar un jardn que guarda la memoria del bosque original. Para los corredores biolgicos, la planificacin es clave. El acompaamiento de profesionales en agroecologa, restauracin o manejo del paisaje facilita identificar reas estratgicas para reforestar, seleccionar especies nativas y definir distancias adecuadas entre plantas. La construccin de estos corredores puede involucrar a familias campesinas, vecinos o asociaciones veredales, fortaleciendo el tejido social a travs de prcticas de cuidado del entorno. La siembra de rboles y arbustos no solo abre rutas para la fauna; tambin estabiliza suelos, protege fuentes hdricas y reduce la erosin.
          .row(titulo="Un compromiso que supera los lmites de la finca")
            .col-12
              p.mb-0 La conservacin rural en Colombia est profundamente vinculada con procesos comunitarios y territoriales. Por esta razn, proteger remanentes y establecer corredores biolgicos trasciende el beneficio individual. Cada accin contribuye a la conectividad del paisaje y a la sostenibilidad ecolgica regional. Al comienzo, estas labores pueden parecer retadoras. Sin embargo, generan una satisfaccin especial cuando se percibe el retorno gradual de la vida silvestre y la recuperacin de las funciones ecolgicas. Ese retorno confirma que cada fragmento cuidado y cada corredor construido fortalecen la permanencia de los ecosistemas en el territorio colombiano.
    .row.mb-4.ms-0
      .col-auto.bg-c5(data-aos="fade-left")
        .row.align-items-end
          .col-auto.px-0
            figure
              img(src='@/assets/curso/tema1/img4.png', alt='', style='width: 40px; position: relative; left: 5px').m-auto
          .col.ps-0
            h3.mb-2.ms-3 Restauracin ecolgica
    p.mb-4 La restauracin ecolgica es un acto de #[b esperanza concreta] en favor del planeta y de la vida que sostiene a las comunidades rurales. En las fincas colombianas, sean pequeas o extensas, este proceso inicia cuando se decide transformar suelos degradados en espacios vivos, donde la #[b diversidad] retoma su lugar y los sonidos naturales del territorio regresan. Restaurar no es solo sembrar rboles: implica #[b acompaar a la naturaleza] para que recupere fortaleza, equilibrio y resiliencia. Los enfoques de restauracin son:
    .row.justify-content-center.mb-5
      .col-11.col-md-8.col-lg-4.bg-c14(data-aos="fade-right")
        .p-4
          figure.mb-4
            img(src='@/assets/curso/tema2/img12.svg', style="width: 120px").m-auto
          p.text-white.fs-3 #[b Restauracin asistida (activa)]
          p.text-white.mb-0 Intervencin directa basada en el manejo de ecosistemas y biodiversidad.
      .col-11.col-md-8.col-lg-4.bg-c15(data-aos="fade-left")
        .p-4
          figure.mb-4
            img(src='@/assets/curso/tema2/img13.svg', style="width: 120px").m-auto
          p.fs-3 #[b Restauracin espontnea (pasiva)]
          p.mb-0 Eliminacin o reduccin de las causas de degradacin para favorecer la regeneracin natural sin obstculos.
    .row.mb-4.ms-0
      .col-auto.bg-c5(data-aos="fade-left")
        .row.align-items-end
          .col-auto.px-0
            figure
              img(src='@/assets/curso/tema1/img4.png', alt='', style='width: 40px; position: relative; left: 5px').m-auto
          .col.ps-0
            h3.mb-2.ms-3 Prcticas de restauracin ecolgica
    p.mb-4 Las estrategias de restauracin se agrupan en cuatro tipos principales, cada una con prcticas especficas segn el nivel de degradacin y los objetivos socioecolgicos:
    .row.bg-c13.mb-5
      .col-12
        .p-5
          .tarjeta.tarjeta--azul1.p-4.shadow
            SlyderA(tipo="b")
              .row.justify-content-center.align-items-center.my-lg-4
                .col-lg-5.mb-4.mb-lg-0
                  p #[b Recuperacin ecolgica o productiva]
                  p Estrategias enfocadas en mejorar la oferta de servicios ecosistmicos de alto valor social, aplicadas en reas con degradacin severa, donde es inviable retornar al estado original.  
                  p.mb-0 Ejemplos: fitorremediacin, obras biomecnicas, revegetalizacin, manejo agroecolgico del suelo, reconversin productiva.
                .col-lg-5
                  figure
                    img(src='@/assets/curso/tema2/img20.png', style="width: 400px").m-auto
              .row.justify-content-center.align-items-center.my-lg-4
                .col-lg-5.mb-4.mb-lg-0
                  p #[b Rehabilitacin funcional]
                  p Busca revitalizar funciones ecolgicas esenciales para mantener y regular los servicios ecosistmicos. Aplicable en zonas con degradacin moderada.  
                  p.mb-0 Ejemplos: enriquecimiento forestal, sistemas agroecolgicos alimentarios, sistemas agroforestales, agrosucesionales, silvopastoriles, tratamientos bioecolgicos como siembra directa, nucleacin o instalacin de perchas.
                .col-lg-5
                  figure
                    img(src='@/assets/curso/tema2/img21.png', style="width: 400px").m-auto
              .row.justify-content-center.align-items-center.my-lg-4
                .col-lg-5.mb-4.mb-lg-0
                  p #[b Restauracin ecolgica]
                  p Orientada a recuperar la estructura, composicin y funcionamiento del ecosistema afectado, buscando un sistema autosostenible.  
                  p.mb-0 Ejemplos: enriquecimientos forestales, manejo de la regeneracin natural, limpieza de caos, ros y humedales, siembra directa o nucleada, revitalizacin de flujos hdricos, control de especies invasoras.
                .col-lg-5
                  figure
                    img(src='@/assets/curso/tema2/img22.png', style="width: 400px").m-auto
              .row.justify-content-center.align-items-center.my-lg-4
                .col-lg-5.mb-4.mb-lg-0
                  p #[b Restauracin multifuncional]
                  p Aborda el manejo integrado del paisaje, articulando objetivos ecolgicos, econmicos y socioculturales.  
                  p.mb-0 Ejemplos: corredores biolgicos, mosaicos agroecolgicos, reconversin productiva, revitalizacin de flujos y fuentes hdricas, enriquecimientos de ecosistemas.
                .col-lg-5
                  figure
                    img(src='@/assets/curso/tema2/img23.png', style="width: 400px").m-auto
    .row.mb-4.ms-0
      .col-auto.bg-c5(data-aos="fade-left")
        .row.align-items-end
          .col-auto.px-0
            figure
              img(src='@/assets/curso/tema1/img4.png', alt='', style='width: 40px; position: relative; left: 5px').m-auto
          .col.ps-0
            h3.mb-2.ms-3 Acciones en el marco del Plan Nacional de Restauracin
    p.mb-4 Los Sistemas Agroforestales (SAF) integran rboles, cultivos y, en algunos casos, animales dentro de una misma unidad productiva. Esta combinacin fortalece la funcionalidad ecolgica del paisaje y genera beneficios econmicos, sociales y culturales para las familias rurales.
    .row.justify-content-center.mb-5
      .col-lg-5.bg-c7.mb-4.mb-lg-0(data-aos="fade-left")
        .row.h-100.align-items-center
            .col-12
              .p-4
                p.mb-0 Los SAF aportan a la transicin agroecolgica al disminuir la presin sobre los ecosistemas naturales, favorecer la conectividad ecolgica entre relictos de bosque y zonas productivas, y consolidar prcticas sostenibles adaptadas al territorio. La diversificacin incrementa la resiliencia frente al cambio climtico, mejora la fertilidad del suelo, regula el microclima y potencia los ciclos de nutrientes.
      .col-lg-auto.ps-lg-0(data-aos="fade-right")
        figure
          img(src='@/assets/curso/tema2/img24.png', style="width: 350px").m-auto
    .row.mb-4.ms-0
      .col-auto.bg-c5(data-aos="fade-left")
        .row.align-items-end
          .col-auto.px-0
            figure
              img(src='@/assets/curso/tema1/img4.png', alt='', style='width: 40px; position: relative; left: 5px').m-auto
          .col.ps-0
            h3.mb-2.ms-3 Tipos de sistemas agroforestales
    p.mb-4 Los #[b sistemas agroforestales] integran rboles, cultivos y, en algunos casos, animales en un mismo espacio productivo. Su propsito es fortalecer la salud del suelo, mejorar la disponibilidad de alimentos y mantener paisajes rurales ms estables y diversos. A continuacin, se describen los tipos ms comunes:
    .row.bg-c13.mb-5
      .col-12
        .p-5
          .tarjeta.tarjeta--azul1.p-4.shadow
            SlyderA(tipo="b")
              .row.justify-content-center.align-items-center.my-lg-4
                .col-lg-5.mb-4.mb-lg-0
                  p #[b Agroforestera tradicional]
                  p Expresin ancestral de interaccin respetuosa con el entorno. Combina cultivos de ciclo corto (maz, frjol) con rboles frutales, maderables o de sombra. No se basa en manuales tcnicos, sino en #[b saberes tradicionales] transmitidos por generaciones.
                  ul.lista-ul.mb-0
                    li.d-flex
                      i.fa-solid.fa-check-circle
                      |
                      p.mb-0 Proporciona alimento inmediato y servicios ecolgicos (proteccin del suelo, aporte de materia orgnica, fijacin de nitrgeno).
                    li.d-flex
                      i.fa-solid.fa-check-circle
                      |
                      p.mb-0 Su objetivo principal es la #[b seguridad alimentaria familiar], no el rendimiento comercial.
                .col-lg-5
                  figure
                    img(src='@/assets/curso/tema2/img25.png', style="width: 400px").m-auto
              .row.justify-content-center.align-items-center.my-lg-4
                .col-lg-5.mb-4.mb-lg-0
                  p #[b Bosques comestibles]
                  p Diseos que imitan la estructura de un bosque natural, organizados por estratos vegetales (suelo, arbustos, dosel medio, rboles altos).
                  ul.lista-ul.mb-0
                    li.d-flex
                      i.fa-solid.fa-check-circle
                      |
                      p.mb-0 Permiten el aprovechamiento eficiente de #[b luz solar, agua y nutrientes].
                    li.d-flex
                      i.fa-solid.fa-check-circle
                      |
                      p.mb-0 Generan microclimas, conservan la humedad, protegen de la erosin y favorecen la #[b resiliencia del ecosistema].
                    li.d-flex
                      i.fa-solid.fa-check-circle
                      |
                      p.mb-0 Producen alimentos, medicinas, maderas y otros recursos de forma sostenible.
                .col-lg-5
                  figure
                    img(src='@/assets/curso/tema2/img26.png', style="width: 400px").m-auto
              .row.justify-content-center.align-items-center.my-lg-4
                .col-lg-5.mb-4.mb-lg-0
                  p #[b Sistemas silvoagrcolas]
                  p Integran #[b cultivos anuales] con #[b hileras de rboles] que cumplen funciones productivas y ecolgicas.
                  ul.lista-ul.mb-0
                    li.d-flex
                      i.fa-solid.fa-check-circle
                      |
                      p.mb-0 Los rboles actan como aliados del cultivo: protegen del viento, regulan la temperatura y #[b aportan materia orgnica].
                    li.d-flex
                      i.fa-solid.fa-check-circle
                      |
                      p.mb-0 Se forman callejones entre lneas de rboles (maderables, frutales o forrajeros) para optimizar luz y nutrientes.
                    li.d-flex
                      i.fa-solid.fa-check-circle
                      |
                      p.mb-0 Fomentan la #[b fertilidad del suelo] y permiten compatibilizar la agricultura con la conservacin.
                .col-lg-5
                  figure
                    img(src='@/assets/curso/tema2/img27.png', style="width: 400px").m-auto
              .row.justify-content-center.align-items-center.my-lg-4
                .col-lg-5.mb-4.mb-lg-0
                  p #[b Sistemas silvopastoriles]
                  p Integran #[b rboles, pasturas y animales] (bovinos, ovinos, caprinos) en una unidad productiva conjunta. Cada componente tiene funciones complementarias que fortalecen la sostenibilidad del sistema.
                  p.mb-0 #[b Funciones principales:]
                  ul.lista-ul.mb-0
                    li.d-flex
                      i.fa-solid.fa-check-circle
                      |
                      p.mb-0 #[b rboles:] Sombra, alimento, mejora del suelo, fijacin de nitrgeno, infiltracin de agua.
                    li.d-flex
                      i.fa-solid.fa-check-circle
                      |
                      p.mb-0 #[b Pasturas:] Variedades resistentes, adaptadas a condiciones difciles.
                    li.d-flex
                      i.fa-solid.fa-check-circle
                      |
                      p.mb-0 #[b Animales:] Manejo sostenible (rotacin, densidades controladas) que evita la degradacin del suelo.
                .col-lg-5
                  figure
                    img(src='@/assets/curso/tema2/img28.png', style="width: 400px").m-auto
              .row.justify-content-center.align-items-center.my-lg-4
                .col-lg-5.mb-4.mb-lg-0
                  p #[b Sistemas silvopastoriles]
                  p.mb-0 #[b Beneficios ecolgicos y productivos:]
                  ul.lista-ul
                    li.d-flex
                      i.fa-solid.fa-check-circle
                      |
                      p.mb-0 Reduccin de la erosin.
                    li.d-flex
                      i.fa-solid.fa-check-circle
                      |
                      p.mb-0 Incremento de la biodiversidad.
                    li.d-flex
                      i.fa-solid.fa-check-circle
                      |
                      p.mb-0 Mayor confort trmico y productividad animal.
                    li.d-flex
                      i.fa-solid.fa-check-circle
                      |
                      p.mb-0 Captura de carbono #[b (mitigacin del cambio climtico)].
                  p.mb-0 Transforman el paisaje ganadero en un sistema productivo y regenerativo.
                .col-lg-5
                  figure
                    img(src='@/assets/curso/tema2/img29.png', style="width: 400px").m-auto
    .row.mb-4.ms-0
      .col-auto.bg-c5(data-aos="fade-left")
        .row.align-items-end
          .col-auto.px-0
            figure
              img(src='@/assets/curso/tema1/img4.png', alt='', style='width: 40px; position: relative; left: 5px').m-auto
          .col.ps-0
            h3.mb-2.ms-3 Corredores florales
    p.mb-4 Los #[b corredores florales] son franjas de vegetacin compuestas por #[b plantas con flores nativas o adaptadas], diseadas para #[b conectar zonas de conservacin, cultivos y reas productivas]. Su funcin principal es #[b favorecer el trnsito de polinizadores, insectos benficos y otras especies clave para el equilibrio ecolgico]. Implementarlos en predios rurales aporta beneficios significativos a nivel ambiental y productivo, ya que:
    .row.justify-content-center.mb-4
      .col-10.col-md-5.col-lg-2.mb-4.mb-lg-0
        .p-4.bg-c6
          figure.mb-3
            img(src='@/assets/curso/tema2/img14.svg', style="width: 90px").m-auto
          p.text-center.text-white.mb-0 Restauran la conectividad ecolgica.
      .col-10.col-md-5.col-lg-2.mb-4.mb-lg-0
        .p-4.bg-c6
          figure.mb-3
            img(src='@/assets/curso/tema2/img15.svg', style="width: 90px").m-auto
          p.text-center.text-white.mb-0 Aumentan la biodiversidad funcional.
      .col-10.col-md-5.col-lg-2
        .p-4.bg-c6
          figure.mb-3
            img(src='@/assets/curso/tema2/img16.svg', style="width: 90px").m-auto
          p.text-center.text-white.mb-0 Mejoran la resiliencia de los agroecosistemas.
    p.mb-4 A continuacin se resumen sus principales funciones agroecolgicas:
    .row.bg-c16
      .col-12
        .px-5
          .row.align-items-center.mb-4
            .col-lg-3.mb-4.mb-lg-0
              figure
                img(src='@/assets/curso/tema2/img31.png', style="width: 300px").m-auto
            .col-lg-9
              SlyderF(columnas="col-md-6 col-xl-4")
                .tarjeta.tarjeta--slyder.p-4
                  .row.justify-content-center.mb-3
                    .col-8
                      img(src="@/assets/curso/tema2/img17.svg", alt="alt").img100.m-auto
                  p.text-center #[b Atraccin de polinizadores]
                  p.text-center.mb-0 Favorecen la presencia de abejas, mariposas y otros insectos que aumentan la productividad de los cultivos.
                .tarjeta.tarjeta--slyder.p-4
                  .row.justify-content-center.mb-3
                    .col-8
                      img(src="@/assets/curso/tema2/img18.svg", alt="alt").img100.m-auto
                  p.text-center #[b Control biolgico natural]
                  p.text-center.mb-0 Hospedan insectos benficos que regulan plagas de forma natural, reduciendo la necesidad de agroqumicos.
                .tarjeta.tarjeta--slyder.p-4
                  .row.justify-content-center.mb-3
                    .col-8
                      img(src="@/assets/curso/tema2/img19.svg", alt="alt").img100.m-auto
                  p.text-center #[b Proteccin del suelo y microclima]
                  p.text-center.mb-0 Contribuyen a reducir la erosin, mejorar la infiltracin del agua y regular la temperatura del entorno.
                .tarjeta.tarjeta--slyder.p-4
                  .row.justify-content-center.mb-3
                    .col-8
                      img(src="@/assets/curso/tema2/img20.svg", alt="alt").img100.m-auto
                  p.text-center #[b Diversificacin paisajstica]
                  p.text-center.mb-0 Integran esttica, cultura y funcionalidad ecolgica, aportando valor agregado al diseo del paisaje productivo.
          p.mb-5 Los corredores florales no solo embellecen el entorno rural, sino que tambin #[b fortalecen los servicios ecosistmicos], siendo una herramienta clave para una agricultura ms sustentable y resiliente.
          .row.mb-4.ms-0
            .col-auto.bg-c5(data-aos="fade-left")
              .row.align-items-end
                .col-auto.px-0
                  figure
                    img(src='@/assets/curso/tema1/img4.png', alt='', style='width: 40px; position: relative; left: 5px').m-auto
                .col.ps-0
                  h3.mb-2.ms-3 Estrategias prediales para el manejo de residuos
    p.mb-4 La gestin de residuos slidos en Colombia representa un reto crucial para reducir impactos ambientales y promover el aprovechamiento de materiales. Dentro de esta problemtica, los residuos orgnicos constituyen una proporcin significativa del total generado, especialmente en zonas rurales.
    .row.bg-c17.mb-5
      .col-12
        .px-5
          .row.justify-content-center.mb-4
            .col-lg-5.bg-c7.mb-4.mb-lg-0(data-aos="fade-left")
              .row.h-100.align-items-center
                  .col-12
                    .p-4
                      p.mb-0 Su correcta gestin, mediante prcticas como el #[b compostaje y la lombricultura], permite avanzar hacia una produccin ms sostenible. Estas estrategias no solo ayudan a #[b cerrar ciclos de nutrientes], sino que tambin #[b generan bioinsumos tiles] para la produccin agrcola.
            .col-lg-auto.ps-lg-0(data-aos="fade-right")
              figure
                img(src='@/assets/curso/tema2/img32.png', style="width: 350px").m-auto
          p.mb-4 El enfoque predial se alinea con los principios de la economa circular, que promueve la #[b separacin en la fuente], el #[b reciclaje], la #[b reutilizacin] y la #[b valorizacin de residuos], convirtindolos en recursos como:
          .row.justify-content-center.mb-4
            .col-10.col-md-5.col-lg-2.mb-4.mb-lg-0
              .p-4.bg-c18
                figure.mb-3
                  img(src='@/assets/curso/tema2/img21.svg', style="width: 90px").m-auto
                p.text-center.mb-0 Bioabonos.
            .col-10.col-md-5.col-lg-2.mb-4.mb-lg-0
              .p-4.bg-c18
                figure.mb-3
                  img(src='@/assets/curso/tema2/img22.svg', style="width: 90px").m-auto
                p.text-center.mb-0 Enmiendas para el suelo.
            .col-10.col-md-5.col-lg-2
